.toggle-button {
    align-self: end;
    justify-self: left;
    margin-left: 50px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 24px;
    width: 30px;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    box-sizing: border-box;
  }

  .toggle-button:focus {
    outline: none;
  }
  
.toggle-button-line {
    width: 30px;
    height: 2px;
    background: black;
  }
