.becomeMode{
    margin-top: 30px;
        -moz-appearance: none;
      -webkit-appearance: none;
        -webkit-tap-highlight-color: transparent;
        color: white;
        width: 200px;
        height: 45px;
        font-family: 'Roboto', sans-serif;
        font-size: 11px;
        text-transform: uppercase;
        letter-spacing: 1.5px;
        font-weight: 600;
        background-color: black;
        border: none;
        border-radius: 10px;
        transition: all 0.3s ease 0s;
        cursor: pointer;
        outline: none;
}
      
